import React, {useState} from 'react'

import AutocompleteAsync from "../autocomplete/autocomplete-async"
import Autocomplete from "../autocomplete/autocomplete"

import db from "./fake-db"
import dbl from "./another-db"
import Delete from "../svg/DeleteButton";
import AddButton from "../svg/AddButton";


let tableRowIndex = 0

function TableRow({row, handleDataChange, deleteRow}) {
    const index = row.index
    const [name, setName] = useState(row.name)
    const [role, setRole] = useState(row.role)
    const [obs, setObs] = useState(row.obs)

    const updateValues = e => {
        let inputName = e.target.name
        let inputValue = e.target.value
        if (inputName === 'name') {
            setName(inputValue)
        } else if (inputName === 'role') {
            setRole(inputValue)
        } else if (inputName === 'obs') {
            setObs(inputValue)
        }

        handleDataChange({
            index: index,
            name: name,
            role: role,
            obs: obs
        })
    }

    const removeRow = () => {
        deleteRow(index)
    }

    const filter = itemList => value => itemList.filter(item => item.role.toLowerCase().includes(value.trim().toLowerCase()))
    const SuggestionCompLocal = ({suggestion}) => <span>{`${suggestion.role}`}</span>
    const displaySuggestionLocal = item => item.role

    const SuggestionComp = ({suggestion}) => <span>{`${suggestion.name}`}</span>
    const displaySuggestion = item => {
        return item.name
    }
    const httpGetter = value => new Promise(
        resolve => {
            setTimeout(() => {
                resolve(
                    () => db.filter(
                        item => item.name
                            .toLowerCase()
                            .includes(value.trim().toLowerCase())))
            }, 500)
        })

    return (
        <tr >
            <td className='flex'>
                <AutocompleteAsync
                    httpGetter={httpGetter}
                    SuggestionComp={SuggestionComp}
                    displaySuggestion={displaySuggestion}
                    onChange={updateValues}
                    value={name}
                    name={name}
                />
            </td>
            <td className='mb-5'>
                <Autocomplete
                    filter={filter(dbl)}
                    SuggestionCompLocal={SuggestionCompLocal}
                    displaySuggestionLocal={displaySuggestionLocal}
                    onChange={updateValues}
                    value={role}
                    name={role}
                />
            </td>
            <td>
                <input type="text" name="obs" className="obs" placeholder="Observatii" value={obs}
                       onChange={updateValues}/>
            </td>
            <td className='ml-4'>
                <button onClick={removeRow}><Delete/></button>
            </td>
        </tr>

    )
}

function Table() {
    const [tableRows, setRows] = useState([{
        name: "",
        role: "",
        obs: ""
    }
    ]);


    // Receive data from TableRow
    const handleChange = data => {
        tableRows[data.index] = data
    }

    // Add New Table Row
    const addNewRow = () => {
        let updatedRows = [...tableRows]
        updatedRows.push({index: tableRowIndex, name: "", role: "", obs: ""})
        setRows(updatedRows)
        console.log('Add:', updatedRows)
    }

    // Remove Table row if rows are count is more than 1
    const deleteRow = (index) => {
        if (tableRows.length > 1) {
            let updatedRows = [...tableRows]
            updatedRows.splice(index, 1)
            setRows(updatedRows);
            console.log('Remove:', updatedRows)
        }
    }

    return (
        <div className="customers">
            <div className='flex'>
                <button onClick={addNewRow} className='flex relative right-0 text-white mb-2'><AddButton className='mr-1 fill-white' /> Adauga un talent</button>
            </div>
            <table className="table w-full " id="customers">
                <thead className=' bg-primary '>
                <tr>
                    <th className='py-2'>Nume</th>
                    <th className='py-2'>Rol</th>
                    <th className='py-2'>Observatii</th>
                    <th className='py-2'>/</th>
                </tr>
                </thead>
                <tbody>
                {
                    tableRows.map((row, index) => {
                        if (row)
                            return (
                                <TableRow
                                    key={index}
                                    row={row}
                                    handleDataChange={() => {
                                        handleChange(index)
                                    }}
                                    deleteRow={() => {
                                        deleteRow(index)
                                    }}>
                                </TableRow>
                            )
                    })
                }
                </tbody>
            </table>

        </div>
    );
}

export default Table;